`, "",{"version":3,"sources":["webpack://./src/components/LoginSetup.vue"],"names":[],"mappings":";AAiGA;CACA,gBAAA;AACA","sourcesContent":["<!--\n  - @copyright 2019 Christoph Wurst <christoph@winzerhof-wurst.at>\n  -\n  - @author 2019 Christoph Wurst <christoph@winzerhof-wurst.at>\n  -\n  - @license GNU AGPL version 3 or any later version\n  -\n  - This program is free software: you can redistribute it and/or modify\n  - it under the terms of the GNU Affero General Public License as\n  - published by the Free Software Foundation, either version 3 of the\n  - License, or (at your option) any later version.\n  -\n  - This program is distributed in the hope that it will be useful,\n  - but WITHOUT ANY WARRANTY; without even the implied warranty of\n  - MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n  - GNU Affero General Public License for more details.\n  -\n  - You should have received a copy of the GNU Affero General Public License\n  - along with this program.  If not, see <http://www.gnu.org/licenses/>.\n  -->\n\n<template>\n\t<div>\n\t\t<div v-if=\"loading\" class=\"loading\" />\n\t\t<SetupConfirmation v-else\n\t\t\t:loading=\"confirmationLoading\"\n\t\t\t:secret=\"secret\"\n\t\t\t:qr-url=\"qrUrl\"\n\t\t\t:confirmation.sync=\"confirmation\"\n\t\t\t@confirm=\"confirm\" />\n\t\t<form ref=\"confirmForm\" method=\"POST\" />\n\t</div>\n</template>\n\n<script>\nimport Logger from '../logger.js'\nimport { saveState } from '../services/StateService.js'\nimport SetupConfirmation from './SetupConfirmation.vue'\nimport STATE from '../state.js'\n\nexport default {\n\tname: 'LoginSetup',\n\tcomponents: {\n\t\tSetupConfirmation,\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tloading: true,\n\t\t\tconfirmationLoading: false,\n\t\t\tsecret: '',\n\t\t\tqrUrl: '',\n\t\t\tconfirmation: '',\n\t\t}\n\t},\n\tmounted() {\n\t\tthis.load()\n\t},\n\tmethods: {\n\t\tload() {\n\t\t\tthis.loading = true\n\t\t\tLogger.info('starting TOTP setup')\n\n\t\t\tsaveState({ state: STATE.STATE_CREATED }).then(\n\t\t\t\t({ secret, qrUrl }) => {\n\t\t\t\t\tLogger.info('TOTP secret received')\n\n\t\t\t\t\tthis.secret = secret\n\t\t\t\t\tthis.qrUrl = qrUrl\n\n\t\t\t\t\tthis.loading = false\n\t\t\t\t},\n\t\t\t)\n\t\t},\n\t\tconfirm() {\n\t\t\tthis.loading = true\n\n\t\t\tsaveState({\n\t\t\t\tstate: STATE.STATE_ENABLED,\n\t\t\t\tcode: this.confirmation,\n\t\t\t}).then(({ state }) => {\n\t\t\t\tif (state === STATE.STATE_ENABLED) {\n\t\t\t\t\tLogger.info('TOTP secret confirmed')\n\n\t\t\t\t\tLogger.info('todo: submit')\n\t\t\t\t\tthis.$refs.confirmForm.submit()\n\t\t\t\t} else {\n\t\t\t\t\tLogger.warn('TOTP confirmation failed')\n\n\t\t\t\t\tthis.loading = false\n\t\t\t\t}\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.loading {\n\tmin-height: 50px;\n}\n</style>\n"],"sourceRoot":""}]);
